@import "./src/assets/themes/appdb/variables.scss";

@import "@/assets/variables";

.filters {
    display: flex;
    gap: 10px;

    @include container(search, 1300px) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-auto-rows: 1fr;
    }
}

.filter {
  flex: 1 0 0;
  min-width: 170px;

  @include container(search, 1300px) {
    min-width: unset;
  }
}
