@import "./src/assets/themes/appdb/variables.scss";

@import "@/assets/variables";

.line {
  display: flex;
  gap: 0.3em;
  white-space: nowrap;
  padding-bottom: 2px;
  max-width: 100%;
}

.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.app {
  display: flex;
  gap: 15px;
  padding: 10px;
  border: 1px solid var(--tertiary-accent);
  border-radius: 5px;
  cursor: pointer;

  @include tablet-container(search) {
    flex-direction: column;
    gap: 10px;
  }
}

.icon {
  flex: 0 0 57px;
  height: 57px;
  width: 57px;
  border-radius: 22.5%;
  mask: url(../../assets/ios-mask.svg) center/100% 100% no-repeat;
  object-fit: cover;
  object-position: center;

  &_stub {
    background-color: var(--stub-bg);
  }
}

.header,
.info,
.details {
  min-width: 0;
}

.header {
  display: flex;
  gap: 15px;
  flex-grow: 1;
}

.name {
  font-size: 16px;
}

.mobile {
  display: none;

  @include tablet-container(search) {
    display: block;
  }
}


.desktop {
  @include tablet-container(search) {
    display: none;
  }
}

.info {
  display: flex;
  flex-direction: column;
  gap: 3px;
}

.compatible-device {
  &:not(:last-child):after {
    content: ', '
  }
}

.details {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 5px;
  margin-left: auto;

  @include tablet-container(search) {
    margin-left: 0;
    align-items: flex-start;
  }
}

.actions {
  display: flex;
  flex-direction: column;
  gap: 5px;
  align-items: stretch;
  white-space: nowrap;
}

.filterable {
  font-size: inherit;
  line-height: inherit;
  color: currentColor;
  text-decoration: underline dotted;
}
